html, body {
  height: 100%;
}

p, h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0;
}

.special__button span {
  margin-bottom: -3px;
}

.pagehead__bg img {
  flex-shrink: 0;
}

.menu_button, .nav__close {
  cursor: pointer;
}

body {
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  position: relative;
}

body > * {
  flex-shrink: 0;
}

body.hidden {
  overflow: hidden;
}

a {
  text-decoration: none;
}

* {
  outline: none !important;
  box-sizing: border-box;
}

input[type='text'], input[type='email'], input[type='password'] {
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}

input::-ms-clear {
  display: none;
}

input::-ms-reveal {
  display: none;
}

.input {
  width: 241px;
  margin-right: 10px;
  padding: 18px 20px;
  background-color: #fff;
  color: #737373;
  font-family: Gilroy;
  font-size: 16px;
  font-weight: 400;
  border: none;
}

.get-bg-img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
  max-width: 100%;
  max-height: 100%;
  opacity: 0;
  pointer-events: none;
}

.set-bg-img {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  overflow: hidden;
}

button {
  cursor: pointer;
  border: none;
}

.left-quot {
  position: absolute;
  right: calc(100% + 20px);
  top: -30px;
}

.popup__img {
  width: 240px;
  height: 240px;
  margin-right: 48px;
  flex-shrink: 0;
}

.popup__teacher.popup {
  display: flex;
  width: 792px;
  flex-direction: row;
  align-items: flex-start;
}

.popup__teacher-name {
  color: #ffffff;
  font-family: "Bebas Neue";
  font-size: 50px;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 40px;
}

.popup__teacher-text {
  color: #ffffff;
  font-family: Gilroy;
  font-size: 18px;
  font-weight: 400;
}

.right-quot {
  position: absolute;
  left: calc(100% + 20px);
  bottom: -30px;
}

.wehelp__item-title {
  font-family: "Bebas Neue";
  font-size: 48px;
  font-weight: bold;
  max-width: 220px;
}

.wehelp__item-button {
  color: #dd222c;
  font-family: "Bebas Neue";
  font-size: 24px;
  font-weight: bold;
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-top: auto;
}

.wehelp__item-button img {
  transition: 0.3s;
  margin-left: 20px;
  max-height: 100%;
}

.wehelp__item-button:hover img {
  transform: translateX(5px);
}

.title {
  position: relative;
  color: #131313;
  max-width: 1115px;
  font-family: "Bebas Neue";
  font-size: 60px;
  font-weight: bold;
  line-height: 60px;
}

.title span {
  color: #dd222c;
}

.get {
  position: relative;
  margin-bottom: 30px;
}

.get__item {
  width: 50%;
  display: flex;
  padding-right: 30px;
}

.get__icon {
  width: 64px;
  height: 64px;
  margin-right: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
}

.get__icon img {
  max-width: 100%;
  max-height: 100%;
}

.get__description p {
  font-family: "Bebas Neue";
  font-size: 36px;
  font-weight: bold;
  line-height: 36px;
  margin-bottom: 25px;
}

.get__description span {
  font-family: "Gilroy";
  font-weight: 400;
  line-height: 22px;
  width: 100%;
  display: inline-block;
  margin-bottom: 20px;
}

.get .title {
  margin: 0 auto 73px auto;
  text-align: center;
  max-width: 930px;
}

.get__container {
  display: flex;
  flex-wrap: wrap;
}

.get__item {
  margin-bottom: 60px;
}

.get .wrapper {
  position: static;
}

.get__bg {
  position: absolute;
  right: 0;
  bottom: 41px;
}

.wehelp {
  margin-bottom: 70px;
}

.wehelp__bg {
  position: absolute;
  right: calc(100% + 58px);
  top: 168px;
}

.wehelp__item-bg {
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
  filter: grayscale(1);
  transition: 0.4s;
}

.wehelp .wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
}

.wehelp .title {
  text-align: center;
  margin: 0 auto 104px auto;
  align-self: baseline;
}

.wehelp__container {
  margin-left: -23px;
  margin-right: -23px;
  display: flex;
  flex-wrap: wrap;
}

.wehelp__item {
  position: relative;
  margin-bottom: 40px;
  overflow: hidden;
  margin-right: 23px;
  margin-left: 23px;
  width: calc(33.3333333% - 46px);
  display: flex;
  flex-direction: column;
  min-height: 328px;
  padding: 48px 40px 40px 40px;
  background: linear-gradient(90deg, whitesmoke 0%, whitesmoke 30%, rgba(245, 245, 245, 0) 100%);
}

.wehelp__item:hover .wehelp__item-bg {
  filter: grayscale(0);
}

.wehelp__item ul, .wehelp__item ol {
  margin-top: 20px;
  margin-bottom: 30px;
}

.wehelp__item ul {
  list-style-type: none;
  padding-left: 10px;
}

.wehelp__item ul li {
  position: relative;
}

