  padding: 70px 60px 60px 60px;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s;
  transform-origin: center;
  transform: scale(0);
  margin: auto;
  align-self: baseline;
  left: 0;
  top: 0;
}

.popups .popup.active {
  transform: scale(1);
  margin: auto;
  position: static;
  pointer-events: auto;
  opacity: 1;
}

