html, body {
    height: 100%;
}
p, h1, h2, h3, h4, h5, h6{
	margin-top: 0;
	margin-bottom: 0;
}
.special__button span{
	margin-bottom: -3px;
}
.pagehead__bg img {
	flex-shrink: 0;	
}
.menu_button, .nav__close{
	cursor: pointer;
}
body{
	display: flex;
    flex-direction: column;
    overflow-x: hidden;
    position: relative;
    & > *{
    	flex-shrink: 0;
    }
    &.hidden{
    	overflow: hidden;
    }
}

a{
	text-decoration: none;
}
*{
	outline: none !important;
	box-sizing: border-box;
}
input[type='text'], input[type='email'], input[type='password'] {    
	outline: none;    
	-webkit-appearance: none;
	-moz-appearance: none;
}
input::-ms-clear {
    display: none;
}
input::-ms-reveal {
	display: none;
}
.input{
	width: 241px;
	margin-right: 10px;
	padding: 18px 20px;
	background-color: #fff;
	color: #737373;
	font-family: Gilroy;
	font-size: 16px;
	font-weight: 400;
	border: none;
}
.get-bg-img {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 0;
	max-width: 100%;
	max-height: 100%;
	opacity: 0;
	pointer-events: none;
}
.set-bg-img {
	background-position: center center;
	background-size: cover;
	background-repeat: no-repeat;
	position: relative;
	overflow: hidden; 
}
button{
	cursor: pointer;
	border: none;
}
.left-quot{
	position: absolute;
	right: calc(100% + 20px);
	top: -30px;
}
.popup__img{
	width: 240px;
	height: 240px;
	margin-right: 48px;
	flex-shrink: 0;
}
.popup__teacher.popup{
	display: flex;
	width: 792px;
	flex-direction: row;
	align-items: flex-start;
}
.popup__teacher-name{
	color: #ffffff;
	font-family: "Bebas Neue";
	font-size: 50px;
	font-weight: bold;
	text-transform: uppercase;
	margin-bottom: 40px;
}
.popup__teacher-text{
	color: #ffffff;
	font-family: Gilroy;
	font-size: 18px;
	font-weight: 400;
}
.right-quot{
	position: absolute;
	left: calc(100% + 20px);
	bottom: -30px;
}
.wehelp__item-title{
	font-family: "Bebas Neue";
	font-size: 48px;
	font-weight: bold;
	max-width: 220px;
}
.wehelp__item-button{
	color: #dd222c;
	font-family: "Bebas Neue";
	font-size: 24px;
	font-weight: bold;
	display: flex;
	align-items: center;
	cursor: pointer;
	margin-top: auto;
	img{
		transition: 0.3s;
		margin-left: 20px;
		max-height: 100%;
	}
	&:hover{
		img{
			transform: translateX(5px);
		}
	}
}
.title{
	position: relative;
	color: #131313;
	max-width: 1115px;
	font-family: "Bebas Neue";
	font-size: 60px;
	font-weight: bold;
	line-height: 60px;
	span{
		color: #dd222c;
	}
}
.get{
	position: relative;
	margin-bottom: 30px;
	&__item{
		width: 50%;
		display: flex;
		padding-right: 30px;
	}
	&__icon{
		width: 64px;
		height: 64px;
		margin-right: 40px;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-shrink: 0;
		img{
			max-width: 100%;
			max-height: 100%;
		}
	}
	&__description{
		p{
			font-family: "Bebas Neue";
			font-size: 36px;
			font-weight: bold;
			line-height: 36px;
			margin-bottom: 25px;
		}
		span{
			font-family: "Gilroy";
			font-weight: 400;
			line-height: 22px;
			width: 100%;
			display: inline-block;
			margin-bottom: 20px;
		}
	}
	.title{
		margin: 0 auto 73px auto;
		text-align: center;
		max-width: 930px;

	}
	&__container{
		display: flex;
		flex-wrap: wrap;
	}
	&__item{
		margin-bottom: 60px;
	}
	.wrapper{
		position: static;
	}
	&__bg{
		position: absolute;
		right: 0;
		bottom: 41px;
	}
}
.wehelp{
	margin-bottom: 70px;
	&__bg{
		position: absolute;
		right: calc(100% + 58px);
		top: 168px;
	}
	&__item-bg{
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		position: absolute;
		z-index: -1;
		filter: grayscale(1);
		transition: 0.4s;
	}
	.wrapper{
		display: flex;
		flex-direction: column;
		position: relative;
	}
	.title{
		text-align: center;
		margin: 0 auto 104px auto;
		align-self: baseline;
	}
	&__container{
		margin-left: -23px;
		margin-right: -23px;
		display: flex;
		flex-wrap: wrap;
	}
	&__item{
		position: relative;
		margin-bottom: 40px;
		overflow: hidden;
		margin-right: 23px;
		margin-left: 23px;
		width: calc(33.3333333% - 46px);
		display: flex;
		flex-direction: column;
		min-height: 328px;
		padding: 48px 40px 40px 40px;
		&:hover{
			.wehelp__item-bg{
				filter: grayscale(0);
			}
		}
		background: linear-gradient(90deg, rgba(245,245,245,1) 0%, rgba(245,245,245,1) 30%, rgba(245,245,245,0) 100%);
		ul, ol{
			margin-top: 20px;
			margin-bottom: 30px;
		}
		ul{
			list-style-type: none;
			padding-left: 10px;
			li{
				position: relative;
			}
			li::before{
				content: '';
				position: absolute;
				right: calc(100% + 4px);
			}
		}
		li{
			font-family: "Gilroy";
			font-weight: 400;
			line-height: 24px;
		}
	}
}
.banner{
	width: 100%;
	min-height: 735px;
	height: calc(100vh - 167px);
	padding-top: 80px;
	padding-bottom: 60px;
	background-color: #f5f5f5;
	position: relative;
	overflow: hidden;
	margin-bottom: 110px;
	.button{
		position: relative;
		z-index: 3;
	}
	&__bg{
		position: absolute;
		bottom: 0px;
		left: 50%;
		transform: translateX(-50%);
		filter: grayscale(1);
		z-index: 2;
		max-height: 500px;
	}
	&__title{
		font-family: "Bebas Neue";
		font-weight: bold;
		display: flex;
		flex-direction: column;
		align-items: center;
		p{
			margin-top: 0;
			margin-bottom: 0;
			color: #212121;
			font-size: 190px;
			line-height: 160px;
		}
		span{
			color: #dd222c;
			font-size: 50px;
		}
	}
	.wrapper{
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
		height: 100%;
		position: static;
	}
}


.wrapper{
	width: 1440px;
	margin: 0 auto;
}
.header{
	position: relative;
	z-index: 10;
	.wrapper{
		display: flex;
		flex-wrap: wrap;
	}
	&__block{
		width: 33.3333333%;
		display: flex;
		align-items: center;
	}
	&__block:nth-child(3){
		justify-content: flex-end;
	}
	&__block:nth-child(2){
		justify-content: center;
	}
	&__container{
		display: flex;
		align-items: center;
		padding: 20px 0px;
		width: 100%;
	}
	&__link{
		display: flex;
		align-items: center;
		color: #212121;
		font-family: "Bebas Neue";
		font-size: 30px;
		font-weight: bold;
		margin-left: 15px;
		transition: 0.3s;
		img{
			margin-right: 10px;
		}
		&:hover{
			color: #dd222c;
		}
	}
	&__link-red{
		color: #dd222c;
		font-family: "Bebas Neue";
		font-size: 24px;
		font-weight: bold;
		display: flex;
		align-items: center;
		img{
			margin-left: 15px;
			transition: 0.3s;
		}
		&:hover{
			img{
				transform: translateX(5px);
			}
		}
	}
	.button{
		margin-left: 28px;
	}
}
.button{
	color: #ffffff;
	font-family: "Bebas Neue";
	font-size: 24px;
	font-weight: bold;
	background-color: #dd222c;
	padding: 15px 26px 12px 26px;
	cursor: pointer;
	transition: 0.3s;
	&:hover{
		background-color: darken(#dd222c, 4%);
	}
}
nav{
	width: 100%;
	border-top: 1px solid #e3e8ed;
	display: flex;
	position: relative;
	z-index: 3;
}
.nav__close{
	display: none;
	position: absolute;
	right: 30px;
	top: 30px;
	width: 40px;
	height: 40px;
	&::before{
		content: '';
		width: 2px;
		height: 40px;
		background-color: #e03942;
		position: absolute;
		left: 20px;
		top: 0;
		transform: rotate(-45deg);
	}
	&::after{
		content: '';
		width: 2px;
		height: 40px;
		background-color: #e03942;
		position: absolute;
		left: 20px;
		top: 0;
		transform: rotate(45deg);
	}

}
.nav__link{
	flex-grow: 1;
	position: relative;
	&:not(:last-child){
		.nav__link-header{
			position: relative;
			&::before{
				position: absolute;
				right: 0;
				top: 50%;
				transform: translateY(-50%);
				content: '';
				width: 6px;
				height: 6px;
				border-radius: 50%;
				background-color: #e3e8ed;
				z-index: -1;
			}
		}
	}
}
.nav__link-header{
	width: 100%;
	display: flex;
	justify-content: center;
	color: #212121;
	font-family: "Bebas Neue";
	font-size: 24px;
	font-weight: bold;
	padding: 20px 0px;
	transition: 0.3s;
	cursor: pointer;
	user-select: none;
	&:hover{
		color: #dd222c;
	}
	&.active{
		background-color: #dd222c;
		color: #fff;
	}
}
.nav__link-container{
	position: absolute;
	width: 100%;
	left: 0;
	top: 100%;
	background-color: #dd222c;
	display: none;
	p{
		font-family: "Bebas Neue";
		font-size: 24px;
		font-weight: bold;
		margin-top: 0;
		margin-bottom: 0;
	}
	span{
		font-family: Gilroy;
		font-size: 16px;
		font-weight: 400;
		width: 100%;
		display: inline-block;
	}
	.nav__link-link{
		display: flex;
		flex-direction: column;
		color: #ffffff;
		padding: 18px 30px;
		transition: 0.3s;
		position: relative;
		a{
			color: #fff;
		}
		&:hover{
			background-color: rgba(255,255,255,0.1);
			.nav__link-inner{
				pointer-events: auto;
				opacity: 1;
			}
		}
	}
}
.nav__link-inner{
	position: absolute;
	top: 0px;
	left: 100%;
	width: 200px;
	background-color: #dd222c;
	transition: 0.3s;
	pointer-events: none;
	opacity: 0;
	&.left{
		left: auto;
		right: 100%;
	}
	a{
		padding: 8px 10px;
		width: 100%;
		text-align: center;
		display: inline-block;
        font-family: Gilroy;
	    font-size: 16px;
	    transition: 0.3s;
	    &:hover{
	    	background-color: rgba(255,255,255,0.1);
	    }
	}
	@media only screen and (max-width: 1000px){
		position: static;
		width: 100%;
		opacity: 1;
		pointer-events: auto;
		background-color: transparent;
		margin-top: 5px;
		display: none;
	}
}
#map{
	width: 100%;
	height: 100%;
	filter: grayscale(1);
}
#map__zoom{
	position: absolute;
	right: 0;
	top: 0;
	color: #ffffff;
	font-family: "Gilroy";
	font-size: 24px;
	padding: 5px;
}
.zoom__add, .zoom__deny{
	user-select: none;
	width: 30px;
	height: 30px;
	border-radius: 4px;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-bottom: 5px;
	background-color: #212121;
	cursor: pointer;
}
.footer{
	width: 100%;
	display: flex;
	margin-top: auto;
	&__map{
		background-color: gray;
		position: relative;
	}
	&__map, &__content{
		width: 50%;
	}
	&__block{
		width: 50%;
	}
	&__block{
		padding-left: 30px;
	}
	&__block:first-child{
		border-right: 1px solid rgba(255,255,255,0.2);
		padding-left: 0;
	}
	&__content{
		background-color: #212121;
		color: #fff;
		padding-top: 60px;
		display: flex;
		justify-content: flex-end;
	}
	&__wrapper{
		width: 720px;
	}
	&__copyright{
		color: rgba(255,255,255,0.2);
		font-family: Gilroy;
		font-size: 14px;
		font-weight: 400;
		margin-top: 50px;
		display: flex;
		justify-content: space-between;
		padding-right: 40px;
		padding-bottom: 27px;
		width: 100%;
		span{
			max-width: 291px;
			display: inline-block;
		}
		a{
			display: inline-block;
			color: #ffffff;
			opacity: 0.2;
			transition: 0.3s;
			&:hover{
				opacity: 0.8;
			}
		}
	}
	&__wrapper{
		display: flex;
		flex-wrap: wrap;
	}
	&__links{
		display: flex;
		flex-direction: column;
		margin-bottom: 35px;
		align-items: flex-start;
		a{
			color: #fff;
			font-family: Gilroy;
			font-size: 16px;
			font-weight: 400;
			line-height: 30px;
			position: relative;
			&::before{
				position: absolute;
				left: 0;
				bottom: 4px;
				width: 0%;
				height: 1px;
				background-color: #fff;
				content: '';
				transition: 0.3s;
			}
			&:hover::before{
				width: 100%;
			}
		}
	}
	&__logo{
		margin-bottom: 35px;
		display: inline-block;
	}
	&__title{
		color: #ffffff;
		font-family: "Bebas Neue";
		font-size: 36px;
		font-weight: bold;
		padding-left: 20px;
		margin-bottom: 10px;
	}
	&__social{
		div{
			max-width: 40px;
			max-height: 40px;
			margin-right: 21px;
		}
		span{
			max-width: 225px;
		}
		a{
			display: flex;
			align-items: center;
			color: #fff;
			font-family: Gilroy;
			font-size: 16px;
			font-weight: 400;
			img{
				max-width: 50px;
				max-height: 50px;
				transition: 0.3s;
			}
			&:hover{
				img{
					transform: scale(1.1);
				}
			}
		}
	}
	&__button{
		display: flex;
		align-items: center;
		color: #ffffff;
		font-family: "Bebas Neue";
		font-size: 24px;
		font-weight: bold;
		margin-top: 15px;
		cursor: pointer;
		img{
			margin-left: 20px;
			max-height: 100%;
			max-width: 70px;
			transition: 0.3s;
		}
		&:hover{
			img{
				transform: translateX(5px);
			}
		}
	}
}
.map__link:hover{
	background-color: darken(#303030, 2%);
}
.map__link.active{
	background-color: #303030;
}
.map__link{
	cursor: pointer;
	transition: 0.3s;
	padding: 17px;
	width: 100%;
	& > *:not(:last-child){
		margin-bottom: 5px;
	}
	& > *{
		display: flex;
		align-items: flex-start;
