			margin-right: 18px;
			margin-top: 4px;
		}
		color: #ffffff;
		font-family: Gilroy;
		font-size: 14px;
		font-weight: 400;
		line-height: 18px;
		div{
			display: flex;
			flex-direction: column;
			align-items: flex-start;
		}
	}
	span{
		display: inline-block;
	}
	a{
		display: inline-block;
		color: #ffffff;
	}
}
