html body .loader-button{
	position: relative;
	&::after, &::before{
		display: none;
	}
	&.loader-active{
		color: transparent;
		pointer-events: none;
		&::after, &::before{
			display: block;
		}
		& > *{
			opacity: 0;
		}
	}
	&::before{
		content: '';
		position: absolute;
	  	border: 4px solid #fff;
	  	opacity: 1;
	  	width: 30px;
	  	height: 30px;
	  	left: 50%;
	  	top: 50%;
	  	border-radius: 50%;
	  	animation: animate-button 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
	  	animation-delay: -0.5s;
	}
	&::after{
		content: '';
		position: absolute;
	  	border: 4px solid #fff;
	  	opacity: 1;
	  	width: 30px;
	  	height: 30px;
	  	left: 50%;
	  	top: 50%;
	  	border-radius: 50%;
	  	animation: animate-button 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
	}
}

@keyframes animate-button {
  0% {
  	transform: translate(-50%, -50%) scale(0.2);
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translate(-50%, -50%) scale(1.5);
  }
}
